.logo{
    height: 70px;
    -o-object-fit: contain;
       object-fit: contain;
       margin-left: 15px;
       -webkit-filter: invert(1);
               filter: invert(1);
}


.icons{
    margin-right: 20px;
    color: #fff;
    cursor: pointer;
}
.av{
    margin-right: 5px;
}


.search{
    flex: 1;
    /* border: 1px solid black; */
    display: flex;
    margin-right: 25px;
   

}


.search div:nth-child(1){
    flex: 0.9;
    /* border-right: 1px solid lightgray; */
    /* padding: 6px 0; */
color: #fff;

}

.search div:nth-child(1) > input{
    width: 100%;
    border: none;
    outline: none;
 
    color: rgb(255, 255, 255);
    background: #121212;
    padding: 7px 0 7px 10px;
   
}
.search div:nth-child(2){
    flex: 0.1;
    padding: 7px 0;
    background: #313131;
    cursor: pointer;
  
}
.search__icon{
    margin-left: 18px;
}

.burger{
 font-size: 100px;
