.sidebar{
    /* flex-basis: 5%; */

    display: flex;
    height: 92vh;
    /* justify-content: right; */
    background: #232425;
    position: fixed;
    top: 10%;
    left: 0;

 
  
}
ul{
    padding: 0;
    margin: 0;
}
ul li{
    list-style: none;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px 3px;
    color: #fff;
    font-size: 14px;
}
ul li:hover{
    background: rgba(211, 211, 211, 0.139);
    cursor: pointer;
}
ul li:hover  .cc{
color: red;
transition: 0.3s;
}

a{
    text-decoration: none;
 
}
.cc{
    color: #fff;
    
}

.h{
    color: #fff;
}

 .menu{
    background: #fff;
    position: fixed;
    top: 0;
    left:0;
    z-index: 9999;
    height: 100vh;
    width: 20%;
    transform: translateX(-100%);
}
.menu.active{
    transform: translateX(0%);
}

a{
    color: #fff;
}
