.home{
    margin-left: 6%;
    margin-top:0%;

    width: 94%;
    background: #121212;
    margin-bottom: 0;
    padding-bottom: 0;




}

/* .video{
    width: 100%;
   padding-left: 84px;
    
   } */
   
   .top{
       height: 50px;
       width: 100%;
       background: #232425;
       display: flex;
       align-items: center;
       padding: 0 10px;
    border-top: 1px solid rgba(255, 255, 255, 0.154);
    border-bottom: 1px solid rgba(255, 255, 255, 0.154);
    position: -webkit-sticky;
    position: sticky;
    top: 10%;
    z-index: 100;
    left: 5%;
  
   }
   .top > button{
       padding: 4px 15px;
       border: none;
       border-radius: 21px;
       margin: 0 7px;
       background: #313131;
       color: #fff;
   }
   button:hover{
       background: rgba(255, 255, 255, 0.17);
   }
   button:first-child{
       background: #fff;
       color: black;
   }
   .kk{
       background: #181818;
       display: flex;
       flex-wrap: wrap;
       justify-content: space-between;
   }



/* videocard */
.videos{
    height: 300px;
    
    width: 300px;
    color: #fff;
}

h5{
    font-size: 14px;
    cursor: pointer;
}
p{
    margin: 0;
    padding: 0;
    font-size: 14px;
    cursor: pointer;
    color: rgba(255, 247, 247, 0.58);
}
.av{
    margin-left: 8px;
    cursor: pointer;
}

.imgbox{
    cursor: pointer;
}

.t:hover{
    color: #fff;

}

@media (min-width:480px){
.home{
    margin-left: 0%;
}
.top{
    width: 107%;
}
}


@media (min-width:1200px){
    .home{
        margin-left: 6%;
    }
    .top{
        width: 100%;
    }
    }