.link{
    margin-left: 6%;
    margin-top:0%;
}

video{
    height:474px;
    width: 100%;
    -o-object-fit: contain;
       object-fit: contain;
}
.tt{
    height: 50px;
    width: 100%;
    background: #121212;
    display: flex;
    align-items: center;
    padding: 0 10px;
    padding-left: 10px;
 /* border-top: 1px solid rgba(255, 255, 255, 0.154);
 border-bottom: 1px solid rgba(255, 255, 255, 0.154); */
 position: -webkit-sticky;
 position: sticky;
 top: 10%;
 z-index: 100777;
 left: 6%;
}
.tt > button{
    padding: 4px 15px;
    border: none;
    border-radius: 21px;
    margin: 0 7px;
    background: #313131;
    color: #fff;
    font-size: 12px;
}
button:hover{
    background: rgba(255, 255, 255, 0.17);
}
.aka{
    padding-left: 32px;
}

h5{
    font-size: 18px;
}
.ll{
    font-size: 12px;
    color: #3073ac;
}
.views{
    font-size: 12px;
    color: #949494;
}
.bb >p{
    margin: 0 10px;
}
.btns{
    margin-right: 4px;
}
.gg{
    border-bottom: 0.5px solid rgb(70, 67, 67);
   
}
button.bt1{
    border: none;
    background: #cc0000;
    color: #fff;
    padding: 5px 19px;
    font-weight: 500;
    font-size: 14px;
    letter-spacing: 0.5px;
}
.tl{
    color: aliceblue;
}

button.bt2{
    border: none;

    color: 
    #7e7e7e;
    padding: 5px 19px;
    font-weight: 500;
    font-size: 14px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    background: #303030;
}
.ff >p:first-child{
    font-size: 16px;
    color: #fafafa;
    padding-left: 5px;
}
.ff >p:last-child{
    font-size: 14px;
    color: #fafafa;
}
.bb >input{
border: none;
background:#121212;
outline: none;
border-bottom: 0.5px solid rgb(70, 67, 67);
width: 100%;
padding-bottom: 5px;
}
::-webkit-input-placeholder{
color: #949494;
}
::-moz-placeholder{
color: #949494;
}
:-ms-input-placeholder{
color: #949494;
}
::-ms-input-placeholder{
color: #949494;
}
::placeholder{
color: #949494;
}
.rr{
    font-size: 12px;
    padding-left: 9px;
}

.rep{
    position: relative;
  ;
}

.rep p{
    letter-spacing: 0.5px;
    color: 
    #3c9ff3;
}

.triangle{
   position: absolute;
   border-style: solid;
   border-color: #3c9ff3 transparent transparent transparent;
  border-width:  8px;
  width: 0px;
  top: 40%;
  left: 1%;
 
 
}

.pp >p:first-child{
    font-size: 14px;
    color: #fff;
}
.pp >p:last-child{
    font-size: 13px;
    color: #fff;
    letter-spacing: 0.5px;
}


